.product_details .productd_img{flex:1 1 40%;}
.product_details .productd_img img{width:100%; height:500px; object-fit: contain;}
.product_details .productd_text{flex:1 1 40%}
.product_details .productd_text h3{font-size: 1.4rem;}
.product_details .productd_text .new_price{font-size: 1.5rem;}
.product_details .productd_text .old_price{font-size: 1rem; color: red;}
.product_details .productd_text p{color: gray; font-size:1rem;}
.product_details .productd_text .product_size_list{display: flex; gap:0.4rem;}
.product_details .productd_text .product_size_list .product_size_item{
border: 1px solid gainsboro; height:45px; width:45px; display: flex; justify-content: center;
align-items: center; font-size: 0.9rem;}
.product_details .productd_text .product_size_list .product_size_item:hover{
    background: var(--color1); color: var(--color2); cursor: pointer;
    transition: all 0.2s linear; border:none;
}