.header .logo img{width:auto; height:50px; object-fit: contain;}
.header .nav .nav_list .nav_item{padding: 1.5rem 0; font-size: 1.1rem;}
.header .cart_div .card_item img{width:22px; height:22px; cursor: pointer;}
.header .cart_div .cart_item_cart{position: relative;} 
.header .cart_div .cart_item_cart .my_values{position: absolute; top:-40%; right: -40%;
height:17px; width:17px; background: var(--color1); color:var(--color2);
font-size: 0.7rem; border-radius: 50%; display: flex; justify-content:center;
align-items: center;}
.header .close img{height:40px; width:40px; display: none;}
.header .open{display: none;}

@media (max-width:992px) {
.header .close img{ display: block; margin: 2rem;}
.header .open{display: block;}
.header .nav{position: fixed; top:0; right:-100%; width:70%; height:100vh;
background: var(--color2); z-index:1000; transition: all 0.4s linear;}
.header .nav.activate{right:0;}
.header .nav .nav_list{display: block; }
.header .nav .nav_list .nav_item{padding:0.8rem 2rem;}
}