.item{width:calc(26% - 2rem)}
.item .item_img img{width:100%; height:350px; object-fit: cover; border-radius:0.5rem;
margin-bottom: 0.5rem;}
.popular h2{text-align: center; margin-bottom: 1.5rem; font-size:1.7rem;}
.item .item_text .product_price{font-size: 1.2rem;}
.item .item_text .old_price{color: red; font-size: 0.9rem;}

@media (max-width:992px) {
    .item{width:calc(35% - 2rem)}
    .item .item_img img{height:300px;}
}

@media (max-width:600px) {
    .item{width:calc(50% - 1rem)}
    .item .item_img img{height:200px;}
}