*{margin: 0; padding: 0; box-sizing: border-box; font-family:Verdana, Geneva, Tahoma, sans-serif;}
:root{
  --color1:#000;
  --color2:#fff;
  --color3:rgb(3, 109, 151);
}
body{scroll-behavior: smooth;}
html{overflow-x: hidden;}
ul li{list-style: none;}
a{text-decoration: none; color: unset;}

/* -------- commen classes --------- */
.w-width{width:80%; margin: 0 auto;}
.d-flex{display: flex;}
.justify-center{justify-content: center;}
.justify-between{justify-content: space-between;}
.justify-end{justify-content:flex-end;}
.justify-start{justify-content: flex-start;}
.align-center{align-items: center;}
.align-end{align-items: end;}
.align-start{align-items: start;}
.flex-column{flex-direction: column;}
.flex-wrap{flex-wrap: wrap;}
.gap1{gap: 0.5rem;}
.gap2{gap:1rem;}
.gap3{gap: 1.5rem;}
.gap4{gap:2rem;}
.gap5{gap:2.5rem;}
.gap6{gap:3rem;}
/*-------------- */
.mt{margin-top: 0.5rem;}
.mt1{margin-top: 1rem;}
.mt2{margin-top: 1.5rem;}
.mt3{margin-top: 2rem;}
.py{padding: 3rem 0;}
.text-center{text-align: center;}

/* ------- commen butotn -------- */
.primary_btn{display: inline-block; padding: 1rem 2.5rem; border-radius: 3rem;
background: var(--color1); color: var(--color2); cursor: pointer;}
.primary_btn:hover{background-color: var(--color3); transition:all 0.3s linear;}
